/* Arrow animation */

.shake-vertical {
	-webkit-animation: shake-vertical 2.5s cubic-bezier(0.455, 0.030, 0.515, 0.955) both;
	        animation: shake-vertical 2.5s cubic-bezier(0.455, 0.030, 0.515, 0.955) both;
}

